.random-character-block{
    display: flex;
}
.random-character-block img{
    width: 300px;
    height: 300px;
    border-radius: 50px;
}
.random-character-block h4{
    font-size: 27px;
    margin: 0px;
    max-width: 500px;
}
.random-character-block-info{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    margin-left: 30px;
}
.random-character-block-info p{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
}
.random-character-block-info p > svg{
    border-radius: 6px;
    background: #fff;
    box-shadow: 0px 6.15355px 16.40946px 2.05118px #EDEDED;
    margin-right: 10px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.random-character-block-info a{
    font-size: 24px;
    font-family:Poppins-ExtraBold;
}

