.info-block-character-container{
    display: flex;

}
.info-block-character-container img{
    width: 300px;
    height: 300px;
    border-radius: 50px;
}
.info-block-character-text{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    margin-left: 30px;
}
.info-block-character-text p{
    margin: 0;
    display: flex;
    align-items: center;
}
.info-block-character-text >p>svg{
    border-radius: 6px;
    background: #fff;
    box-shadow: 0px 6.15355px 16.40946px 2.05118px #EDEDED;
    margin-right: 10px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}