.nav-bar{
    height: 100%;
    margin-right: 15px;
}
.nav-list{
    margin:  0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: #474A51;
}
.nav-list a{
    width: 215px;
    height: 45px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    font-family: 'Poppins-Bold';
}
.nav-list a  > span{
    font-size: 18px;
    margin: 5px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-list a > p {
    margin: 0;
}
.nav-list a:hover{
    background: #8692A6;
    border-radius: 50px;
    color: #fff;
}