
.wrapper {
    max-width: 1440px;
    margin: 0 auto;
}
.title-page{
    margin-top: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
}
.title-page-info{
    background-color: #fff;
    border-radius: 50px;
    padding: 25px;
    width: 100%;
}

.main-page-container{
    margin: 15px 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
